<template>
  <div name="navBar" style="z-index: 1">
    <transition name="slide-fade">
      <v-app-bar
        flat
        :fixed="isAppbarFixed"
        :color="appbarColor"
        height="84"
        extension-height="0"
        v-scroll="onScroll"
      >
        <v-spacer class="hidden-sm-and-down"></v-spacer>
        <v-img src="static/images/tfv-logo.png" alt="Travelfreak Vacations" max-width="40"></v-img>
        <v-toolbar-title class="white--text ml-2">Travelfreak Vacations</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn
          v-for="(link, i) in links"
          :key="i"
          text
          class="white--text hidden-sm-and-down"
          tile
          exact
          @click="navigateTo(link)"
        >{{ link.text }}</v-btn>
        <v-spacer class="hidden-sm-and-down"></v-spacer>

        <v-app-bar-nav-icon class="white--text hidden-md-and-up" @click="navDrawer=!navDrawer" />
        <v-navigation-drawer
          v-model="navDrawer"
          absolute
          temporary
          right
          style="top:55px"
          height="280"
        >
          <v-list nav dense>
            <v-list-item v-for="(link, i) in links" :key="i">
              <v-list-item-title @click="navigateTo(link)">{{ link.text }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-navigation-drawer>
      </v-app-bar>
    </transition>
  </div>
</template>

<script>
import router from "@/router";
//import appbarColor from "@/components/mixins/appbarColor.js";

export default {
  name: "NavBar",
  //mixins: [appbarColor],
  data: () => {
    return {
      links: [
        { href: "/", text: "Home", appbarColor: "blue darken-4" },
        { href: "/about", text: "About", appbarColor: "cyan accent-4" },
        { href: "/tours", text: "Tours", appbarColor: "light-blue darken-2" },
        {
          href: "/affiliations",
          text: "Affiliations",
          appbarColor: "orange darken-4"
        },
        {
          href: "/testimonials",
          text: "Testimonials",
          appbarColor: "blue-grey lighten-1"
        },
        { href: "/contact", text: "Contact", appbarColor: "#0270A0" }
      ],
      navDrawer: false,
      isAppbarFixed: false,
      appbarColor: "blue darken-4"
    };
  },
  methods: {
    navigateTo(link) {
      this.navDrawer = false;
      this.setAppbarColor(link.appbarColor);

      //console.log("router.path: " + this.$route.path + ", link.href: " + link.href);
      if (this.$route.path !== link.href) {
        router.push({ path: link.href });
      }
    },
    setAppbarColor(color) {
      //console.log("setAppbarColor: " + color);
      this.appbarColor = color;
    },
    onScroll() {
      let s = window.pageYOffset || document.documentElement.scrollTop;
      this.isAppbarFixed = s > 500;
    }
  },
  created() {
    // Set appbarColor appropriate for the current route
    for (let i in this.links) {
      if (this.$route.path === this.links[i].href) {
        //console.log("Matched: " + this.$route.name + ", " + this.links[i].href);
        this.setAppbarColor(this.links[i].appbarColor);
        return;
      }
    }
  }
};
</script>

<style scoped>
.v-btn {
  text-transform: none;
}
.v-toolbar {
  font-weight: 700;
  font-size: 20px;
}
</style>


