import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../components/HomePage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: () => import(/* webpackChunkName: "about" */ '../components/AboutPage.vue').then(m => m.default)
  },
  {
    path: '/tours',
    name: 'tours',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "tours" */ '../components/ToursPage.vue').then(m => m.default)
  },
  {
    path: '/affiliations',
    name: 'affiliations',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "affiliations" */ '../components/AffiliationsPage.vue').then(m => m.default)
  },
  {
    path: '/testimonials',
    name: 'testimonials',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "testimonials" */ '../components/TestimonialsPage.vue').then(m => m.default)
  },
  {
    path: '/contact',
    name: 'contact',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "contact" */ '../components/ContactPage.vue').then(m => m.default)
  },
  {
    path: '*',
    name: 'catchAll',
    component: HomePage
  }
]

const router = new VueRouter({
  routes,
  history: true,
  mode: 'history'
})

export default router;



